import React from "react";
import "./About.css";
import Titlebar from "../commons/Titlebar";

const About = ({ windowIsMaximized, texts, onChildAction }) => {
  return (
    <div id="about">
      <Titlebar
        caller={{
          name: "about",
          onClose: () => onChildAction({ component: { name: "main" } }),
        }}
        windowIsMaximized={windowIsMaximized}
        onChildAction={onChildAction}
      />
      <header className="title">
        {texts.find((text) => text.code === "about-title").value}
      </header>
      <main className="content">
        <header>
          <div>
            <span>AppName</span>
            <span>v1.0.0</span>
          </div>
          <div>
            Kjn dnc ksnc sndckd ncn cknsdc ksnck dncknskcnskdncksnck nskc dnc
            knc ksnc dcs
          </div>
        </header>
        <main>
          <div>MAIN</div>
        </main>
      </main>
    </div>
  );
};

export default About;
