import React, { Component } from "react";
import "./Profile.css";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import icons from "../../../assets/images/icons.svg";
import Albums from "./Albums";
import Playlist from "./Playlist";
import Seasons from "./Seasons";
import Movie from "./Movie";
import Book from "./Book";
import Author from "./Author";
import Course from "./Course";
import Titlebar from "../../commons/Titlebar";
import ProfileEditor from "./ProfileEditor";
import { getStoredItem } from "../../../storage/storage";
import { getAge } from "../../../utils/utils";

export default class Profile extends Component {
  original;
  state = { editing: false, lastStatus: null, languageKey: null };

  constructor(props) {
    super(props);
    this.original = JSON.parse(JSON.stringify(props.data));
    document.documentElement.lang = props.language;

    // {storage:"music.name", id:"",position:11111}
    const storage =
      (props.category === "music"
        ? props.data.medias
          ? "playlist"
          : "music"
        : props.category) +
      "." +
      props.data[props.language.replace("-", "_")].title;
    const status = getStoredItem(storage);
    this.state.lastStatus = status ? status : { storage, id: "", position: 0 };
    this.state.languageKey = props.language.replace("-", "_");

    console.log(
      ">>",
      props.language,
      props.data[props.language.replace("-", "_")]
    );
  }

  componentDidUpdate(prevProps, prevState) {
    const { data, category, updateLastStatus, onChildAction, language } =
      this.props;
    if (updateLastStatus) {
      onChildAction({
        component: {
          name: "profile",
          profile: data,
        },
      });
      const storage =
        (category === "music"
          ? data.medias
            ? "playlist"
            : "music"
          : category) +
        "." +
        data[this.state.languageKey].title;
      const status = getStoredItem(storage);
      this.setState({
        ...this.state,
        lastStatus: status ? status : { storage, id: "", position: 0 },
      });
    }

    if (prevProps.language !== language) {
      this.setState({ ...this.state, languageKey: language.replace("-", "_") });
    }
  }

  onToggleDescription = () => {
    const description = document.querySelector("#profile .right .description");
    description.classList.toggle("collapsed");
    setTimeout(() => {
      if (description) description.classList.toggle("ellipsis");
    }, 200);

    const button = document.querySelector("#profile .description .collapse");
    button.classList.toggle("hide");
  };

  getLinkByLanguage = (link, language) => {
    let name = "";
    if (typeof link.name === "string") name = link.name;
    else if (typeof link.name === "object") {
      if (language.replace("-", "_") in link.name)
        name = link.name[language.replace("-", "_")];
      else {
        const keys = Object.keys(link.name);
        if (keys.length > 0) name = link.name[keys[0]];
      }
    }
    return {
      name,
      url: link.url,
    };
  };

  onEdit = () => {
    this.setState({ ...this.state, editing: true });
  };

  onSave = () => {
    this.setState({ ...this.state, editing: false });
  };

  render() {
    const {
      data,
      category,
      languages,
      language,
      texts,
      playingId,
      onChildAction,
      server,
      windowIsMaximized,
    } = this.props;
    const { editing, lastStatus, languageKey } = this.state;
    return (
      <>
        <Titlebar
          caller={{
            name: "profile",
            isRemote: server.isRemote,
            isEditing: editing,
            onEdit: () => this.onEdit(),
            // onSave: () => this.onSave(),
            onClose: () => {
              if (editing) this.onSave();
              else
                onChildAction({
                  component: { name: "main" },
                });
            },
          }}
          windowIsMaximized={windowIsMaximized}
          onChildAction={onChildAction}
        />
        {editing ? (
          <ProfileEditor
            data={data}
            languages={languages}
            language={language}
            texts={texts}
            category={category}
            server={server}
            onChildAction={this.onSave}
            playingId={playingId}
            windowIsMaximized={windowIsMaximized}
          />
        ) : (
          <div id="profile" className={`selectable${editing ? " editor" : ""}`}>
            <header className="top">
              <div className="aka">{data[languageKey].aka}</div>
              <div className="title">{data[languageKey].title}</div>
              {category === "books" && data.medias && (
                <div className="subtitle">{data.subtitle}</div>
              )}
            </header>
            <div className="container">
              <div className="left">
                <div className="tags">
                  {data[languageKey].tags.map((tag, idx, arr) => (
                    <span key={tag}>
                      {tag}
                      {idx < arr.length - 1 ? ", " : ""}
                    </span>
                  ))}
                </div>
                <div className="previews">
                  {data.previews.length > 0 && (
                    <img
                      className={`${
                        data.previews.length < 2 ? "expanded" : ""
                      }`}
                      src={`${
                        data.previews[0].type !== "url"
                          ? server.url + "/api/image/"
                          : ""
                      }${data.previews[0].image
                        .replace(/\\/g, "%5C")
                        .replace(/\//g, "%5C")
                        .replace("#", "%23")}`}
                      alt={data.previews[0].type}
                      onClick={() => {
                        if (data.previews[0].image !== "") {
                          onChildAction({
                            preview: {
                              items: data.previews,
                              index: 0,
                            },
                          });
                        }
                      }}
                    />
                  )}
                  {data.previews.length > 1 && (
                    <div className="previews-items">
                      <Swiper
                        spaceBetween={0}
                        freeMode={false}
                        slidesPerView={"auto"}
                      >
                        {data.previews.map((preview, idx) => (
                          <SwiperSlide
                            style={{ width: "auto", overflow: "hidden" }}
                            key={idx}
                            onClick={() => {
                              if (data.previews[idx].image !== "") {
                                onChildAction({
                                  preview: {
                                    items: data.previews,
                                    index: idx,
                                  },
                                });
                              }
                            }}
                          >
                            <div
                              className="preview-item"
                              style={{
                                backgroundImage: `url("${
                                  preview.type !== "url"
                                    ? server.url + "/api/image/"
                                    : ""
                                }${preview.thumb
                                  .replace(/\\/g, "%5C")
                                  .replace(/\//g, "%5C")
                                  .replace("#", "%23")}")`,
                              }}
                            ></div>
                          </SwiperSlide>
                        ))}
                      </Swiper>
                    </div>
                  )}
                </div>
              </div>
              <div className="right">
                {data[languageKey].infos && (
                  <div className="infos">
                    {data[languageKey].infos.map((pair, idx) => (
                      <div key={pair.key} className="info">
                        <div>{pair.key}</div>
                        <div>
                          {pair.value +
                            (data["en"].infos[idx].key === "birth date" &&
                            (!data["en"].infos.find(
                              (i) => i.key === "death date"
                            ) ||
                              data["en"].infos.find(
                                (i) => i.key === "death date"
                              ).value === "")
                              ? " (" + getAge(...pair.value.split(".")) + ")"
                              : "")}
                        </div>
                      </div>
                    ))}
                  </div>
                )}
                {data[languageKey].description !== "" && (
                  <div
                    className={`description scrollbar${
                      data.medias && data.medias.length < 3 ? " greater" : ""
                    }`}
                  >
                    <div
                      className="collapse hide"
                      onClick={() => this.onToggleDescription()}
                    >
                      <svg width="8" height="8">
                        <use xlinkHref={`${icons}#down`} />
                      </svg>
                    </div>
                    {data[languageKey].description}
                  </div>
                )}
                {(data.collections ||
                  (data.medias && data.medias.length > 1)) && (
                  <div className="collection-label">
                    {
                      texts.find(
                        (text) =>
                          text.code ===
                          category +
                            (data.collections ? "-collections" : "-items")
                      ).value
                    }
                    <strong>
                      {" "}
                      (
                      {data.collections
                        ? data.collections.length
                        : data.medias.length}
                      )
                    </strong>
                  </div>
                )}
                <div
                  className={`collection-container scrollbar${
                    category === "courses" ? " course" : ""
                  }`}
                >
                  {category === "music" && data.collections && (
                    <Albums
                      languageKey={languageKey}
                      lastStatus={lastStatus}
                      collections={data.collections}
                      playingId={playingId}
                      onChildAction={onChildAction}
                      server={server}
                    />
                  )}
                  {category === "music" && data.medias && (
                    <Playlist
                      languageKey={languageKey}
                      lastStatus={lastStatus}
                      medias={data.medias}
                      playingId={playingId}
                      onChildAction={onChildAction}
                      server={server}
                    />
                  )}
                  {category === "series" && (
                    <Seasons
                      languageKey={languageKey}
                      lastStatus={lastStatus}
                      collections={data.collections}
                      playingId={playingId}
                      onChildAction={onChildAction}
                      server={server}
                    />
                  )}
                  {category === "movies" && (
                    <Movie
                      languageKey={languageKey}
                      lastStatus={lastStatus}
                      medias={data.medias}
                      playingId={playingId}
                      onChildAction={onChildAction}
                      server={server}
                    />
                  )}
                  {category === "books" && data.medias && (
                    <Book
                      languageKey={languageKey}
                      medias={data.medias}
                      onChildAction={onChildAction}
                      server={server}
                    />
                  )}
                  {category === "books" && data.collections && (
                    <Author
                      languageKey={languageKey}
                      collections={data.collections}
                      onChildAction={onChildAction}
                      server={server}
                    />
                  )}
                  {category === "courses" && (
                    <Course
                      languageKey={languageKey}
                      lastStatus={lastStatus}
                      collections={data.collections}
                      medias={data.medias}
                      playingId={playingId}
                      onChildAction={onChildAction}
                      server={server}
                    />
                  )}
                </div>
              </div>
            </div>
            {data[languageKey].links.length > 0 && (
              <div className="links">
                {data[languageKey].links.map((link) => (
                  <a
                    key={link.name}
                    href={'"#"'}
                    onClick={(e) => {
                      e.preventDefault();
                      onChildAction({
                        action: {
                          name: "open-link",
                          value: link.url,
                        },
                      });
                    }}
                  >
                    {link.name}
                  </a>
                ))}
              </div>
            )}
          </div>
        )}
      </>
    );
  }
}
