import React, { Component } from "react";
import { getMediasList } from "../../remote/requests";
import Titlebar from "../commons/Titlebar";
import "./PlaylistEditor.css";

export default class PlaylistEditor extends Component {
  state = { mediaslist: null };

  componentDidMount() {
    getMediasList(this.props.server.url)
      .then((mediaslist) => {
        console.log(mediaslist);
        this.setState({ ...this.state, mediaslist });
      })
      .catch((error) => {
        // fatal error
        // dialog -> try again?
        console.log(error);
      });
  }

  render() {
    const { data, texts, playingId, onChildAction, server, windowIsMaximized } =
      this.props;
    return (
      <div id="playlist">
        <Titlebar
          caller={{
            name: "playlist",
            isRemote: server.isRemote,
            onClose: () =>
              onChildAction({
                component: { name: "main" },
              }),
          }}
          windowIsMaximized={windowIsMaximized}
          onChildAction={onChildAction}
        />
      </div>
    );
  }
}
