  closeMenus = (e) => {
    const menuElement = document.querySelector("#main-menu");
    const btnElement = document.querySelector("#main-menu-btn");
    try {
      if (
        !menuElement ||
        !menuElement.classList ||
        (menuElement.classList && menuElement.classList.contains("hidden")) ||
        btnElement.contains(e.target)
      )
        return;

      const fPos = getElementOffset(menuElement);
      const fW = menuElement.offsetWidth;
      const fH = menuElement.offsetHeight;
      const insideFilter =
        e.clientX >= parseInt(fPos.left) &&
        e.clientX <= parseInt(fPos.left) + fW &&
        e.clientY >= parseInt(fPos.top) &&
        e.clientY <= parseInt(fPos.top) + fH;

      if (!insideFilter) menuElement.classList.add("closed");
    } catch (error) {}

    const tagElement = document.querySelector("#tags");
    const tagBtnElement = document.querySelector("#hashtag");
    try {
      if (
        !tagElement ||
        !tagElement.classList ||
        (tagElement.classList && tagElement.classList.contains("hidden")) ||
        tagBtnElement.contains(e.target)
      )
        return;

      const fPos = getElementOffset(tagElement);
      const fW = tagElement.offsetWidth;
      const fH = tagElement.offsetHeight;
      const insideFilter =
        e.clientX >= parseInt(fPos.left) &&
        e.clientX <= parseInt(fPos.left) + fW &&
        e.clientY >= parseInt(fPos.top) &&
        e.clientY <= parseInt(fPos.top) + fH;

      if (!insideFilter) tagElement.classList.add("closed");
    } catch (error) {}

    const profileElement = document.querySelector("#editor-menu");
    const profileBtnElement = document.querySelector("#profile-edit-btn");
    try {
      if (
        !profileElement ||
        !profileElement.classList ||
        (profileElement.classList &&
          profileElement.classList.contains("hidden")) ||
        profileBtnElement.contains(e.target)
      )
        return;

      const fPos = getElementOffset(profileElement);
      const fW = profileElement.offsetWidth;
      const fH = profileElement.offsetHeight;
      const insideFilter =
        e.clientX >= parseInt(fPos.left) &&
        e.clientX <= parseInt(fPos.left) + fW &&
        e.clientY >= parseInt(fPos.top) &&
        e.clientY <= parseInt(fPos.top) + fH;

      if (!insideFilter) profileElement.classList.add("closed");
    } catch (error) {}
  };

  onKeyDown = (e) => {
    if (e.which === 27) {
      // ESC
      const menuMain = document.querySelector("#main-menu");
      if (menuMain && menuMain.classList) {
        menuMain.classList.add("closed");
      }
      const tags = document.querySelector("#tags");
      if (tags && tags.classList) {
        tags.classList.add("closed");
      }
      const profileElement = document.querySelector("#editor-menu");
      if (profileElement && profileElement.classList) {
        profileElement.classList.add("closed");
      }
      if (this.state.dialog) this.setState({ ...this.state, dialog: null });
      // else if (
      //   this.state.component &&
      //   (this.state.component.name === "imageviewer" ||
      //     this.state.component.name === "email")
      // )
      //   this.setState({ ...this.state, component: null });
    } else if (e.which === 34 || e.which === 40 || e.which === 39) {
      // RIGHT
      const { category, listsState } = this.state;
      this.setState({
        ...this.state,
        listsState: listsState[category].items + 10,
      });
    } else if (e.which === 33 || e.which === 38 || e.which === 37) {
      // LEFT
      const { category, listsState } = this.state;
      this.setState({
        ...this.state,
        listsState: listsState[category].items - 10,
      });
    }
  };

  setTagsByLanguage = (language) => {
    const { prefs, config, lists } = this.state;
    prefs.language = language;

    const languageKey = prefs.language.replace("-", "_");
    const categories = config.categories;
    for (let i = 0; i < categories.length; i++) {
      const category = categories[i];
      category.terms = "";
      const tags = [];
      const languageTags = lists[category.code].tags[languageKey];
      if (languageTags) {
        for (let j = 0; j < languageTags.length; j++) {
          const tag = languageTags[j];
          tags.push({
            label: tag,
            active: false,
          });
        }
      }
      category.tags = tags;
    }

    this.setState({ ...this.state, categories, prefs });
  };

