import React, { Component } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "./Preview.css";
import Titlebar from "./Titlebar";

export default class Player extends Component {
  bodyBGColor;

  state = {
    zoom: {
      oldX: 0,
      oldY: 0,
      x: 0,
      y: 0,
      z: 1,
    },
  };

  constructor(props) {
    super(props);
    this.bodyBGColor = document.body.style.backgroundColor;
    document.body.style.backgroundColor = "rgb(0, 0, 0)";
  }

  // onWheel = (e) => {
  //   e.preventDefault();
  //   const image = document.querySelector("#preview-image img");
  //   if (e.deltaY) {
  //     const sign = Math.sign(e.deltaY) / 10;
  //     const scale = 1 - sign;
  //     const rect = image.getBoundingClientRect();
  //     const before = this.state.zoom;

  //     console.log();
  //     if (rect.width <= image.width || rect.height <= image.height) {
  //       this.setState({ ...this.state, zoom: before });
  //       return;
  //     }
  //     this.setState({
  //       ...this.state,
  //       zoom: {
  //         oldX: before.oldX,
  //         oldY: before.oldY,
  //         x: before.x * scale - (rect.width / 2 - e.clientX + rect.x) * sign,
  //         y:
  //           before.y * scale -
  //           ((image.height * rect.width) / image.width / 2 -
  //             e.clientY +
  //             rect.y) *
  //             sign,
  //         z: before.z * scale,
  //       },
  //     });
  //   }
  // };

  reloadImage = () => {
    this.play(this.props.index);
  };

  componentDidMount() {
    window.wheelzoom(document.querySelector("#preview-image img"));
    window.addEventListener("resize", this.reloadImage);

    const loading = document.querySelector("#preview-loading");
    const videoContainer = document.querySelector("#preview-video");
    const video = document.querySelector("#preview-video video");
    video.onloadedmetadata = () => {
      video.play();
      loading.classList.add("hidden");
      videoContainer.classList.remove("hidden");
    };
    video.onerror = () => {};
    video.onended = () => {};
    this.play(this.props.index);
  }

  componentWillUnmount() {
    document.body.style.backgroundColor = this.bodyBGColor;
    window.removeEventListener("resize", this.reloadImage);
  }

  play = (index) => {
    const { items, server } = this.props;
    const loading = document.querySelector("#preview-loading");
    const videoContainer = document.querySelector("#preview-video");
    const imageContainer = document.querySelector("#preview-image");
    const image = document.querySelector("#preview-image img");
    const video = document.querySelector("#preview-video video");

    video.pause();
    video.currentTime = 0;
    loading.classList.remove("hidden");
    videoContainer.classList.add("hidden");
    imageContainer.classList.add("hidden");

    if (items[index].type === "video") {
      video.src = items[index].image;
    } else {
      const img = new Image();
      img.onload = function () {
        image.src = this.src;
        loading.classList.add("hidden");
        imageContainer.classList.remove("hidden");
      };
      img.src =
        (items[index].type !== "url" ? server.url + "/api/image/" : "") +
        items[index].image.replace(/\\/g, "%5C").replace(/\//g, "%5C").replace("#", "%23");
    }
  };

  onClose = () => {
    console.log("CLOSE");
  };

  render() {
    const { items, onChildAction, server, windowIsMaximized } = this.props;
    const zoom = this.state.zoom;
    return (
      <div id="preview" className={windowIsMaximized ? "noborders " : ""}>
        <div id="preview-loading" className="hidden">
          <div>CARREGANDO</div>
        </div>
        <div id="preview-video" className="hidden">
          <video src="#" controls></video>
        </div>
        <div
          id="preview-image"
          className="hidden zoom"
          style={{
            transform: `translate(${zoom.x}px, ${zoom.y}px) scale(${zoom.z})`,
          }}
        >
          <img src="#" alt="" />
        </div>
        <Titlebar
          caller={{
            name: "preview",
            onClose: () => onChildAction({ preview: null }),
          }}
          windowIsMaximized={windowIsMaximized}
          onChildAction={onChildAction}
        />
        {items.length > 1 && (
          <div className="preview-controls">
            <Swiper spaceBetween={0} freeMode={false} slidesPerView={"auto"}>
              {items.map((item, idx) => (
                <SwiperSlide
                  key={idx}
                  style={{ width: "auto", overflow: "hidden" }}
                  onClick={() => this.play(idx)}
                >
                  <div
                    className={`preview-item${
                      idx === this.index ? " active" : ""
                    }`}
                    style={{
                      backgroundImage: `url("${
                        item.type !== "url" ? server.url + "/api/image/" : ""
                      }${item.thumb
                        .replace(/\\/g, "%5C")
                        .replace(/\//g, "%5C")
                        .replace("#", "%23")}")`,
                      marginLeft: idx === 0 ? "0px" : "1px",
                    }}
                  ></div>
                </SwiperSlide>
              ))}
            </Swiper>
          </div>
        )}
      </div>
    );
  }
}
