import axios from "axios";
const { httpsAgent } = window.electron;

export const remoteControl = (url, command, params) => {
  axios
    .get(url + "/api/app/" + command + "/" + encodeURI(JSON.stringify(params)), url.startsWith("https://") ? { httpsAgent: httpsAgent } : {})
    .then((response) => {
      console.log(response.data);
      if (response.data.error) {
      } else {
      }
    })
    .catch((error) => {
      console.log(error);
    });
};

export const getMediasList = (url) => {
  return new Promise(async (resolve, reject) => {
    axios
      .get(url + "/api/mediaslist", url.startsWith("https://") ? { httpsAgent: httpsAgent } : {})
      .then((response) => {
        if (response.data.error) reject(response.data.error);
        else resolve(response.data);
      })
      .catch((error) => {
        reject(error);
      });
  });
};
