import React from "react";
import icons from "../../assets/images/icons.svg";
import playing from "../../assets/images/playing.gif";
import { durationToTime, getProfileMedias } from "../../utils/utils";

const MediaClickable = ({
  playingId,
  lastStatus,
  media,
  collections,
  medias,
  collectionMediasLength,
  onChildAction,
  server,
  languageKey,
}) => {
  let mediaType = "";
  switch (media.mime) {
    case "application/epub+zip":
      mediaType = "epub";
      break;
    case "application/pdf":
      mediaType = "pdf";
      break;
    case "MANGA":
      break;
    default:
      break;
  }
  return (
    <div
      key={media.id}
      className={`media${
        ((media.category === "books" && medias) ||
          media.category === "movies") &&
        collectionMediasLength === 1
          ? " hovered"
          : ""
      }`}
    >
      {media.track && collectionMediasLength > 1 && (
        <div className="media-track">
          {playingId === media.id
            ? ""
            : media.disc + (media.disc === "" ? "" : " - ") + media.track}
        </div>
      )}
      <div
        className={`media-texts${playingId === media.id ? " playing" : ""}${
          lastStatus && lastStatus.id === media.id ? " last-played" : ""
        }`}
        onClick={(e) => {
          if (media.duration) {
            const collectionMedias = collections
              ? getProfileMedias({ collections })
              : getProfileMedias({ medias });
            const index = collectionMedias.findIndex((m) => m.id === media.id);
            onChildAction({
              player: {
                storage: lastStatus ? lastStatus.storage : "",
                medias: collectionMedias,
                mediaIndex: index,
                mediaPlayingId: "",
              },
            });
          } else {
            onChildAction({
              action: {
                name: "open-file",
                value: media.url,
              },
            });
          }
        }}
      >
        {media[languageKey].aka !== "" && media.category !== "movies" && (
          <div className="media-aka">{media[languageKey].aka}</div>
        )}
        <div className="media-title">{media[languageKey].title}</div>
        {playingId === media.id && (
          <img className="media-playing" src={playing} alt="playing" />
        )}
      </div>
      <div className="media-extra">
        {media.texts && media.texts.length > 0 && (
          <svg width="18" height="18">
            <use xlinkHref={`${icons}#subtitles`} />
          </svg>
        )}
        {media.chords && media.chords.length > 0 && (
          <svg width="14" height="14">
            <use xlinkHref={`${icons}#chords`} />
          </svg>
        )}
        <div className="media-duration">
          {media.duration
            ? durationToTime(media.duration)
            : media.language + " (" + mediaType + ")"}
        </div>
        {server.isRemote && media.duration && (
          <svg
            className="media-onremote"
            width="16"
            height="16"
            onClick={() => {
              const collectionMedias = collections
                ? getProfileMedias({ collections })
                : getProfileMedias({ medias });
              const index = collectionMedias.findIndex(
                (m) => m.id === media.id
              );
              onChildAction({
                action: {
                  name: "play-profile-onremote",
                  value: {
                    mediaIndex: index,
                    mediaPlayingId: "",
                    pieces: [media.category, media[languageKey].profile],
                  },
                },
              });
            }}
          >
            <use xlinkHref={`${icons}#cloud-play`} />
          </svg>
        )}
      </div>
    </div>
  );
};

export default MediaClickable;
