import React, { Component } from "react";
import { setDraggable } from "../../utils/utils";
import "./Controller.css";

export default class Controller extends Component {
  ws;
  enabledCtrl;
  state = { playing: null, theme: null, language: null, subtitles: null };

  componentDidMount() {
    const dragElem = document.querySelector("#controller");
    if (dragElem) setDraggable(dragElem);

    const remoteServer = this.props.servers.remote;
    if (remoteServer) {
      this.enabledCtrl = remoteServer.enabled && remoteServer.controller;
      // this.ws = new WebSocket(
      //   "ws://" + remoteServer.ip + ":" + remoteServer.port
      // );
      this.ws = new WebSocket(
        (remoteServer.protocol === "https" ? "wss":"ws") + "://" + remoteServer.ip + ":" + remoteServer.port
      );
      this.ws.onopen = () => {
        // REGISTER
        this.ws.send(JSON.stringify({ name: "Okimas" }));
      };
      this.ws.onmessage = (message) => {
        try {
          const object = JSON.parse(message.data.toString());
          if ("prefs" in object) {
            this.setState({
              ...this.state,
              theme: object.prefs.theme,
              language: object.prefs.language,
              subtitles: object.prefs.subtitles,
            });
          } else if ("server" in object) {
            this.props.onChildAction({
              action: {
                name: "remote-server",
                value: object.server,
              },
            });
          } else if (this.enabledCtrl) {
            this.setState({ ...this.state, playing: object });
          }
        } catch (error) {}
      };
      this.ws.onerror = (error) => {
        console.log(error);
      };
      this.ws.onclose = () => {};
    }
  }

  componentDidUpdate() {
    const remoteServer = this.props.servers.remote;
    this.enabledCtrl =
      remoteServer && remoteServer.enabled && remoteServer.controller;
  }

  componentWillUnmount() {
    if (this.ws) this.ws.close();
  }

  onThemeChange = (e) => {
    this.setState({ ...this.state, theme: e.target.value });
    return e.target.value;
  };

  onLanguageChange = (e) => {
    this.setState({ ...this.state, language: e.target.value });
    return e.target.value;
  };

  onSubtitlesSizeChange = (e) => {
    const subtitles = this.state.subtitles;
    subtitles.size = e.target.value;
    this.setState({ ...this.state, subtitles });
    return subtitles;
  };

  onSubtitlesColorChange = (e) => {
    const subtitles = this.state.subtitles;
    subtitles.color = e.target.value;
    this.setState({ ...this.state, subtitles });
    return subtitles;
  };

  render() {
    const { languages, texts, onChildAction } = this.props;
    const { theme, language, subtitles, playing } = this.state;
    return (
      <div id="controller">
        <div onClick={() => onChildAction({ controller: null })}>
          CLOSE CTRL
        </div>
        <hr />
        <strong>ARTIST:</strong>
        {playing && (
          <>
            {playing.media && (
              <div>
                Media:{" "}
                {playing.media[language.replace("-", "_")].profile +
                  " - " +
                  playing.media[language.replace("-", "_")].collection +
                  " - " +
                  playing.media[language.replace("-", "_")].title}
              </div>
            )}
            <div>Total: {playing.size}</div>
            <div>Index: {playing.mediaIndex}</div>
            <button
              className="gray rounded"
              onClick={() => {
                this.ws.send(
                  JSON.stringify({
                    command: "play-profile-index",
                    value: playing.size - 1,
                  })
                );
              }}
            >
              <span>play last</span>
            </button>
            <hr />
            <strong>STATUS:</strong>
            <div>{JSON.stringify(playing.status)}</div>
            <strong>subs</strong>
            {subtitles && (
              <div className="options">
                <label>
                  <input
                    type="radio"
                    name="font-size"
                    value={"smallFont"}
                    checked={subtitles.size === "smallFont"}
                    onChange={(e) => {
                      if (this.enabledCtrl) {
                        const subs = this.onSubtitlesSizeChange(e);
                        this.ws.send(
                          JSON.stringify({
                            command: "subtitles-font",
                            value: subs,
                          })
                        );
                      }
                    }}
                  />
                  {
                    texts.find(
                      (text) => text.code === "prefs-subtitles-size-small"
                    ).value
                  }
                </label>
                <label>
                  <input
                    type="radio"
                    name="font-size"
                    value={"mediumFont"}
                    checked={subtitles.size === "mediumFont"}
                    onChange={(e) => {
                      if (this.enabledCtrl) {
                        const subs = this.onSubtitlesSizeChange(e);
                        this.ws.send(
                          JSON.stringify({
                            command: "subtitles-font",
                            value: subs,
                          })
                        );
                      }
                    }}
                  />
                  {
                    texts.find(
                      (text) => text.code === "prefs-subtitles-size-medium"
                    ).value
                  }
                </label>
                <label>
                  <input
                    type="radio"
                    name="font-size"
                    value={"bigFont"}
                    checked={subtitles.size === "bigFont"}
                    onChange={(e) => {
                      if (this.enabledCtrl) {
                        const subs = this.onSubtitlesSizeChange(e);
                        this.ws.send(
                          JSON.stringify({
                            command: "subtitles-font",
                            value: subs,
                          })
                        );
                      }
                    }}
                  />
                  {
                    texts.find(
                      (text) => text.code === "prefs-subtitles-size-big"
                    ).value
                  }
                </label>
                {` `}|{` `}
                <label>
                  <input
                    type="radio"
                    name="font-color"
                    value={"yellowFont"}
                    checked={subtitles.color === "yellowFont"}
                    onChange={(e) => {
                      if (this.enabledCtrl) {
                        const subs = this.onSubtitlesColorChange(e);
                        this.ws.send(
                          JSON.stringify({
                            command: "subtitles-font",
                            value: subs,
                          })
                        );
                      }
                    }}
                  />
                  {
                    texts.find(
                      (text) => text.code === "prefs-subtitles-color-yellow"
                    ).value
                  }
                </label>
                <label>
                  <input
                    type="radio"
                    name="font-color"
                    value={"whiteFont"}
                    checked={subtitles.color === "whiteFont"}
                    onChange={(e) => {
                      if (this.enabledCtrl) {
                        const subs = this.onSubtitlesColorChange(e);
                        this.ws.send(
                          JSON.stringify({
                            command: "subtitles-font",
                            value: subs,
                          })
                        );
                      }
                    }}
                  />
                  {
                    texts.find(
                      (text) => text.code === "prefs-subtitles-color-white"
                    ).value
                  }
                </label>
              </div>
            )}
            <button
              className="gray rounded"
              onClick={() => {
                this.ws.send(
                  JSON.stringify({
                    command: "volume",
                    value: playing.status.volume - 10,
                  })
                );
              }}
            >
              <span>volume -10%</span>
            </button>
            <button
              className="gray rounded"
              onClick={() => {
                this.ws.send(
                  JSON.stringify({
                    command: "volume",
                    value: playing.status.volume + 10,
                  })
                );
              }}
            >
              <span>volume +10%</span>
            </button>
            <button
              className="gray rounded"
              onClick={() => {
                this.ws.send(JSON.stringify({ command: "pause" }));
              }}
            >
              <span>play/pause</span>
            </button>
            <button
              className="gray rounded"
              onClick={() => {
                this.ws.send(JSON.stringify({ command: "preview" }));
              }}
            >
              <span>preview</span>
            </button>
            <button
              className="gray rounded"
              onClick={() => {
                this.ws.send(JSON.stringify({ command: "next" }));
              }}
            >
              <span>next</span>
            </button>
            <button
              className="gray rounded"
              onClick={() => {
                const value =
                  (100 * (playing.status.progress.current - 30.0)) /
                  playing.status.progress.duration;
                this.ws.send(JSON.stringify({ command: "progress", value }));
              }}
            >
              <span>progress -30s</span>
            </button>
            <button
              className="gray rounded"
              onClick={() => {
                const value =
                  (100 * (playing.status.progress.current + 30.0)) /
                  playing.status.progress.duration;
                this.ws.send(JSON.stringify({ command: "progress", value }));
              }}
            >
              <span>progress +30s</span>
            </button>
            <hr />
            {playing.media &&
              playing.media.texts.map((text, idx) => (
                <button
                  key={idx.toString()}
                  className="gray rounded"
                  onClick={() => {
                    this.ws.send(
                      JSON.stringify({ command: "subtitle", value: idx })
                    );
                  }}
                >
                  <span>{text.labels[language.replace("-", "_")]}</span>
                </button>
              ))}
            <hr />
            <strong>PLAYER:</strong>
            <div>
              <button
                className="gray rounded"
                onClick={() => {
                  this.ws.send(
                    JSON.stringify({
                      command: "player-window",
                      value: "minimized",
                    })
                  );
                }}
              >
                <span>minimize</span>
              </button>
              <button
                className="gray rounded"
                onClick={() => {
                  this.ws.send(
                    JSON.stringify({
                      command: "player-window",
                      value: "miniplayer",
                    })
                  );
                }}
              >
                <span>miniplayer</span>
              </button>
              <button
                className="gray rounded"
                onClick={() => {
                  this.ws.send(
                    JSON.stringify({
                      command: "player-window",
                      value: "defaultplayer",
                    })
                  );
                }}
              >
                <span>defaultplayer</span>
              </button>
              <button
                className="gray rounded"
                onClick={() => {
                  this.ws.send(
                    JSON.stringify({
                      command: "player-window",
                      value: "fullscreen",
                    })
                  );
                }}
              >
                <span>fullscreen</span>
              </button>
              <button
                className="gray rounded"
                onClick={() => {
                  this.ws.send(
                    JSON.stringify({ command: "player-window", value: "close" })
                  );
                }}
              >
                <span>close</span>
              </button>
            </div>
            <hr />
            <strong>APP:</strong>
            <div>{JSON.stringify(playing.window)}</div>
          </>
        )}
        <div>
          <strong>theme</strong>
        </div>
        {theme && (
          <div className="options">
            <label>
              <input
                type="radio"
                name="theme"
                value={"light"}
                checked={theme === "light"}
                onChange={(e) => {
                  if (this.enabledCtrl) {
                    const value = this.onThemeChange(e);
                    this.ws.send(
                      JSON.stringify({ command: "theme-set", value: value })
                    );
                  }
                }}
              />
              {texts.find((text) => text.code === "prefs-theme-light").value}
            </label>
            <label>
              <input
                type="radio"
                name="theme"
                value={"dark"}
                checked={theme === "dark"}
                onChange={(e) => {
                  if (this.enabledCtrl) {
                    const value = this.onThemeChange(e);
                    this.ws.send(
                      JSON.stringify({ command: "theme-set", value: value })
                    );
                  }
                }}
              />
              {texts.find((text) => text.code === "prefs-theme-dark").value}
            </label>
            <label>
              <input
                type="radio"
                name="theme"
                value={"system"}
                checked={theme === "system"}
                onChange={(e) => {
                  if (this.enabledCtrl) {
                    const value = this.onThemeChange(e);
                    this.ws.send(
                      JSON.stringify({ command: "theme-set", value: value })
                    );
                  }
                }}
              />
              {texts.find((text) => text.code === "prefs-theme-system").value}
            </label>
          </div>
        )}
        <div>
          <strong>language</strong>
        </div>
        {language && (
          <div className="options">
            {languages.map((lang) => {
              return (
                <label key={lang.code}>
                  <input
                    type="radio"
                    name="language"
                    value={lang.code}
                    checked={language === lang.code}
                    onChange={(e) => {
                      if (this.enabledCtrl) {
                        const value = this.onLanguageChange(e);
                        this.ws.send(
                          JSON.stringify({
                            command: "language-set",
                            value: value,
                          })
                        );
                      }
                    }}
                  />
                  {lang.name}
                </label>
              );
            })}
          </div>
        )}
        <button
          className="gray rounded"
          onClick={() => {
            this.ws.send(JSON.stringify({ command: "window-hide" }));
          }}
        >
          <span>hide</span>
        </button>
        <button
          className="gray rounded"
          onClick={() => {
            this.ws.send(JSON.stringify({ command: "window-minimize" }));
          }}
        >
          <span>minimize</span>
        </button>
        <button
          className="gray rounded"
          onClick={() => {
            this.ws.send(JSON.stringify({ command: "window-maximize" }));
          }}
        >
          <span>maxmize</span>
        </button>
        <button
          className="gray rounded"
          onClick={() => {
            this.ws.send(JSON.stringify({ command: "window-close" }));
          }}
        >
          <span>close app</span>
        </button>
      </div>
    );
  }
}
