import React, { Component } from "react";
import "./Player.css";
import playingBackground from "../../assets/images/background.gif";
import Titlebar from "./Titlebar";
import PlayerControl from "./PlayerControl";
import { setDraggable, unsetDraggable } from "../../utils/utils";
import { getStoredItem, storeItem } from "../../storage/storage";

const clickDelay = 300;
export default class Player extends Component {
  ws;
  state = {
    windowClass: "defaultplayer",
    progress: {
      current: 0,
      duration: 0,
    },
    status: {
      paused: false,
      prev: false,
      next: false,
      textIndex: -1,
    },
    casting: "",
    preferentialLanguage: window.navigator.language,
  };

  clickTimer;
  cursorTimer;
  volumeTimer;
  clicked = false;
  mediaIndex = -1;
  textIndex = -1;
  isLoading;
  bodyBGColor;
  cast = {};

  setWebSocket = () => {
    const localServer = this.props.servers.local;
    if (localServer && localServer.enabled && localServer.controller) {
      this.ws = new WebSocket(
        (localServer.protocol === "https" ? "wss":"ws") + "://" + localServer.ip + ":" + localServer.port
      );
      this.ws.onopen = () => {
        // this.ws.send(JSON.stringify({ name: "player" }));
      };

      this.ws.onmessage = (message) => {
        try {
          const object = JSON.parse(message.data.toString());
          if ("command" in object) {
            if (
              [
                "player-window",
                "pause",
                "progress",
                "volume",
                "preview",
                "next",
                "play-profile-index",
                "subtitle",
                "subtitles-font",
              ].includes(object.command)
            ) {
              this.onControlsAction(object.command, object.value);
            } else
              this.props.onChildAction({
                action: {
                  name: object.command,
                  value: object.value ? object.value : "",
                },
              });
          } else if ("server" in object) {
            this.props.onChildAction({action: {name: "remote-server", value: object.server}});
          }
        } catch (error) {}
      };

      this.ws.onclose = () => {
        //   props.onChildAction({ controller: null });
      };
    }
  };

  constructor(props) {
    super(props);

    this.bodyBGColor = document.body.style.backgroundColor;
    document.body.style.backgroundColor = "rgb(0, 0, 0)";

    this.setWebSocket();

    const status = getStoredItem(this.props.storage);
    if (status) this.state.preferentialLanguage = status.language;

    // let initCastInterval = setTimeout(() => {
    //   if (window.chrome.cast.isAvailable) {
    //     clearInterval(initCastInterval);
    //     this.initializeCastApi();
    //   } else console.log("unavailable");      
    // }, 10000);
  }

  initializeCastApi = () => {
    const sessionRequest = new window.chrome.cast.SessionRequest(window.chrome.cast.media.DEFAULT_MEDIA_RECEIVER_APP_ID);
    const apiConfig = new window.chrome.cast.ApiConfig(
      sessionRequest, 
      (session) => {
        console.log("cast session", session);
        this.cast.session = session;
      }, 
      (availability) => {
        console.log("availability", availability);
        if (availability === window.chrome.cast.ReceiverAvailability.AVAILABLE) {
          console.log("receiver is available");
          this.setState({...this.state, casting:"off"})
        }
      }, 
      window.chrome.cast.AutoJoinPolicy.ORIGIN_SCOPED, 
      window.chrome.cast.DefaultActionPolicy.CREATE_SESSION
    );
    window.chrome.cast.initialize(
      apiConfig, () => { console.log("cast initSuccess") }, (error) => {console.log("cast error", error) }
    );
  };

  castNow = () => {
    console.log("casting!");
    const sessionRequest = new window.chrome.cast.SessionRequest(
      window.chrome.cast.media.DEFAULT_MEDIA_RECEIVER_APP_ID
    );
    window.chrome.cast.requestSession(
      (session) => {
        console.log("Session success", session);
        this.cast.session = session;
        this.setState({...this.state, casting:"on"}, () => {
          this.castCurrentMedia();
        });
      },
      (error) => {
        console.log("onLaunchError", error);
      },
      sessionRequest
    );
  };

  castCurrentMedia = () => {
    if(!this.cast.session) return;
    const { data, servers, language } = this.props;
    const media = data[this.mediaIndex];
    const mediaUrl =
      (servers.isRemote ? servers.remote.url : servers.local.url) +
      "/api/media/" +
      media.url
        .replace(/\\/g, "%5C")
        .replace(/\//g, "%5C")
        .replace("#", "%23") +
      "/" +
      media.mime.replace("/", "-");

    let subtitle;
    if (media.texts.length > 0) {
      const vttUrl = servers.isRemote
        ? servers.remote.url
        : servers.local.url +
          "/api/file/" +
          media.texts[0].file
            .replace(/\\/g, "%5C")
            .replace(/\//g, "%5C")
            .replace("#", "%23");
      subtitle = new window.chrome.cast.media.Track(
        1, // track ID
        window.chrome.cast.media.TrackType.TEXT
      );
      subtitle.trackContentId = vttUrl;
      subtitle.trackContentType = "text/vtt";
      subtitle.subtype = window.chrome.cast.media.TextTrackType.SUBTITLES;
      subtitle.name = media.texts[0].labels[language.replace("-", "_")];
      subtitle.language = media.texts[0].language;
      subtitle.customData = null;
    }

    console.log("LoadNow, session:", this.cast.session);
    const mediaInfo = new window.chrome.cast.media.MediaInfo(
      mediaUrl,
      media.mime
    );
    if (subtitle) {
      mediaInfo.tracks = [subtitle];
      mediaInfo.activeTrackIds = [1];
    }
    const request = new window.chrome.cast.media.LoadRequest(mediaInfo);
    this.cast.session.loadMedia(
      request,
      onMediaDiscovered.bind(this, "loadMedia"),
      function (er) {
        console.log("onMediaError", er);
      }
    );

    function onMediaDiscovered(how, media) {
      console.log("got media!", media);
      this.cast.currentMedia = media;
      var activeTrackIds = [1];
      var tracksInfoRequest =
        new window.chrome.cast.media.EditTracksInfoRequest(activeTrackIds);
      media.editTracksInfo(
        tracksInfoRequest,
        function succCB() {
          console.log("success changing trackIDs!");
        },
        function errorCallback() {
          console.log("Error CB!");
        }
      );
    }
  };

  /**===================================== */

  updatePlayerController = () => {
    if (this.ws) {
      const { data, volume, windowIsMaximized } = this.props;
      const video = document.querySelector("#player-video video");
      const playing = {
        size: data.length,
        mediaIndex: this.mediaIndex,
        textIndex: this.textIndex,
        media: data[this.mediaIndex],
        window: {
          windowIsMaximized,
          windowClass: this.state.windowClass,
        },
        status: {
          paused: video.paused,
          prev: this.mediaIndex > 0,
          next: this.mediaIndex < data.length - 1,
          volume,
          progress: {
            current: video.currentTime,
            duration: video.duration,
          },
        },
      };
      this.ws.send(JSON.stringify(playing));
    }
  };

  updateStatus = () => {
    const { data, onChildAction, storage } = this.props;
    const video = document.querySelector("#player-video video");
    if (video) {
      this.setState(
        {
          ...this.state,
          status: {
            paused: video.paused,
            prev: this.mediaIndex > 0,
            next: this.mediaIndex < data.length - 1,
            textIndex: this.textIndex,
          },
        },
        () => {
          onChildAction({
            player: {
              storage,
              medias: data,
              mediaIndex: this.mediaIndex,
              mediaPlayingId: data[this.mediaIndex].id,
            },
          });
        }
      );
    }
  };

  togglePlay = () => {
    const controls = document.querySelector("#player .player-controls");
    const video = document.querySelector("#player-video video");
    if (video.paused) {
      video.play();
      controls.classList.remove("visible");
    } else {
      video.pause();
      controls.classList.add("visible");
    }
    this.showMouseCursor();
    this.updatePlayerController();
  };

  setVideoSize = () => {
    const { windowClass } = this.state;
    const video = document.querySelector("#player-video video");
    if (video) {
      const isAudio = video.getAttribute("data-audio") === "true";
      const isMini = windowClass !== "defaultplayer";
      if (isAudio) {
        video.style.width = "100%";
        video.style.height = "100%";
        video.style.objectFit = "cover";
      } else if (isMini) {
        video.style.width = "auto";
        video.style.height = "auto";
      } else {
        const screenWidth = window.innerWidth;
        const screenHeight = window.innerHeight;
        const screenWideness = screenWidth / screenHeight;
        const videoWidth = video.videoWidth;
        const videoHeight = video.videoHeight;
        const videoWideness = videoWidth / videoHeight;
        if (screenWideness > videoWideness) {
          video.style.width = "auto";
          video.style.height = screenHeight + "px";
        } else {
          video.style.width = screenWidth + "px";
          video.style.height = "auto";
        }
      }
    }
  };

  hideMouseCursor = () => {
    const video = document.querySelector("#player-video video");
    if (video && !video.paused)
      document.querySelector("#player-video").classList.add("nocursor");
  };

  showMouseCursor = () => {
    clearTimeout(this.cursorTimer);
    document.querySelector("#player-video").classList.remove("nocursor");
    // if (this.state.windowClass === "defaultplayer") {
    if (!document.querySelector("#player-video video").paused) {
      this.cursorTimer = setTimeout(() => {
        this.hideMouseCursor();
      }, 1000);
    }
    // }
  };

  showVolume = () => {
    clearTimeout(this.volumeTimer);
    document.querySelector("#player-volume").classList.remove("hidden");
    this.volumeTimer = setTimeout(() => {
      document.querySelector("#player-volume").classList.add("hidden");
    }, 2000);
  };

  componentDidMount() {
    const { data, mediaIndex, volume, subtitles, onChildAction } = this.props;
    this.mediaIndex = mediaIndex;
    const loading = document.querySelector("#player-loading");
    const videoContainer = document.querySelector("#player-video");
    const video = document.querySelector("#player-video video");
    window.addEventListener("resize", this.setVideoSize, false);

    video.onmousemove = this.showMouseCursor;
    video.onmousedown = this.showMouseCursor;
    video.ontouchstart = this.showMouseCursor;
    video.ontouchmove = this.showMouseCursor;
    video.onkeydown = this.showMouseCursor;
    video.onwheel = (e) => {
      if (e.deltaY < -50) {
        const vUp = video.volume + 0.05 > 1 ? 1 : video.volume + 0.05;
        video.volume = vUp;
        onChildAction({
          action: { name: "volume-set", value: Math.round(vUp * 100) },
        });
        this.showVolume();
      } else if (e.deltaY > 50) {
        const vDn = video.volume - 0.05 < 0 ? 0 : video.volume - 0.05;
        video.volume = vDn;
        onChildAction({
          action: { name: "volume-set", value: Math.round(vDn * 100) },
        });
        this.showVolume();
      }
    };

    video.classList.add(subtitles.size);
    video.classList.add(subtitles.color);

    video.volume = parseFloat(volume / 100);
    video.onclick = (e) => {
      this.showMouseCursor();
      if (this.clicked) {
        this.clicked = false;
        clearTimeout(this.clickTimer);
        if (data[this.mediaIndex].mime.startsWith("video/")) {
          video.requestFullscreen();
          // onChildAction({ action: { name: "window-maximize" } });
        }
      } else {
        this.clicked = true;
        this.clickTimer = setTimeout(() => {
          this.clicked = false;
          this.togglePlay();
        }, clickDelay);
      }
    };
    video.onloadedmetadata = () => {
      video.play();
      loading.classList.add("hidden");
      videoContainer.classList.remove("hidden");
      this.updateStatus();
      // onChildAction({
      //   player: {
      //     medias: data,
      //     mediaIndex: this.mediaIndex,
      //     mediaPlayingId: data[this.mediaIndex].id,
      //   },
      // });
    };
    video.oncanplay = () => {
      this.setVideoSize();
      // setTimeout(() => {
      //   this.canSetStatus = true;
      // }, 60000);
    };
    video.onerror = (e) => {
      // console.log(e.target.error, "XXX", video.error);
      if (e.target.error || video.error) {
        console.log(e.target.error, "YYY", video.error);
        this.mediaIndex++;
        this.play();
      }
    };
    video.onended = () => {
      this.mediaIndex++;
      this.play();
    };
    const context = this;
    video.onpause = () => {
      context.updateStatus();
    };
    video.onplay = () => {
      context.updateStatus();
    };

    this.play();

  }

  progressInterval;
  setTracks = (video, mediaTexts, index) => {
    this.textIndex = -1;
    const tracks = document.querySelectorAll("#player-video video track");
    for (let i = tracks.length - 1; i >= 0; i--) {
      tracks[i].parentNode.removeChild(tracks[i]);
    }

    if (mediaTexts.length > 0 && index !== -2) {
      for (let i = 0; i < mediaTexts.length; i++) {
        if (mediaTexts[i].language === this.state.preferentialLanguage) {
          this.textIndex = i;
          break;
        }
      }
      for (let i = 0; i < mediaTexts.length; i++) {
        const text = mediaTexts[i];
        const track = document.createElement("track");
        track.kind = "subtitles";
        track.srclang = text.language;
        track.label = text.labels[this.props.language.replace("-", "_")];
        if (index === -1) {
          if (this.textIndex === -1) {
            track.default = i === 0 ? "default" : "";
            track.mode = i === 0 ? "showing" : "hidding";
            if (i === 0) this.textIndex = i;
          } else {
            track.default = i === this.textIndex ? "default" : "";
            track.mode = i === this.textIndex ? "showing" : "hidding";
          }
        } else {
          track.default = i === index ? "default" : "";
          track.mode = i === index ? "showing" : "hidding";
          this.textIndex = index;
          //XXX
        }
        const url = this.props.servers.isRemote ? this.props.servers.remote.url : this.props.servers.local.url;
        track.src = url + "/api/file/" + text.file.replace(/\\/g, "%5C").replace(/\//g, "%5C").replace("#", "%23");

        video.appendChild(track);
      }
      if (index !== -1) this.updateStatus();
    }
    if (index === -2) {
      this.textIndex = -1;
      this.updateStatus();
    }
    if (this.textIndex > -1)
      this.setState({
        ...this.state,
        preferentialLanguage:
          mediaTexts[this.textIndex].language !== ""
            ? mediaTexts[this.textIndex].language
            : this.state.preferentialLanguage,
      });
  };

  play = () => {
    this.isLoading = true;
    const { data, servers } = this.props;
    if (this.mediaIndex < data.length) {
      const loading = document.querySelector("#player-loading");
      const videoContainer = document.querySelector("#player-video");
      const video = document.querySelector("#player-video video");

      video.pause();
      video.currentTime = 0;
      loading.classList.remove("hidden");
      videoContainer.classList.add("hidden");

      const media = data[this.mediaIndex];
      if (media.mime.startsWith("audio/")) {
        video.poster = playingBackground;
        video.setAttribute("data-audio", true);
      } else {
        // video.poster = null;
        video.setAttribute("data-audio", false);
      }

      this.setTracks(video, media.texts, -1);
      video.type = media.mime;
      const url = servers.isRemote ? servers.remote.url : servers.local.url;
      video.src =
        url +
        "/api/media/" +
        media.url.replace(/\\/g, "%5C").replace(/\//g, "%5C").replace("#", "%23") + 
        "/" + media.mime.replace("/", "-");

      clearInterval(this.progressInterval);
      this.progressInterval = setInterval(() => {
        if (!video.paused) {
          const progress = {
            current: video.currentTime,
            duration: video.duration,
          };
          this.setState({ ...this.state, progress }, () => {
            this.updatePlayerController();
          });
        }
      }, 1000);
    } else this.props.onChildAction({ player: null });
    setTimeout(() => {
      this.isLoading = false;
    }, 300);
  };

  componentDidUpdate(prevProps, prevState) {
    if (!this.ws) this.setWebSocket();

    const { windowClass } = this.state;
    const dragElem = document.querySelector("#player");
    if (dragElem) {
      if (windowClass === "miniplayer") setDraggable(dragElem);
      else unsetDraggable(dragElem);
    }

    if (!this.isLoading) {
      const { playingId, mediaIndex } = this.props;
      if (playingId === "") {
        this.mediaIndex = mediaIndex;
        this.play();
      }
    }

    if (
      JSON.stringify(prevProps.subtitles) !==
      JSON.stringify(this.props.subtitles)
    ) {
      console.log("S", prevProps.subtitles, this.props.subtitles);
      const video = document.querySelector("#player-video video");
      video.classList.remove(...video.classList);
      // video.classList.remove(prevProps.subtitles.size);
      // video.classList.remove(prevProps.subtitles.color);
      video.classList.add(this.props.subtitles.size);
      video.classList.add(this.props.subtitles.color);
    }
    if (
      prevProps.windowIsMaximized !== this.props.windowIsMaximized ||
      prevProps.volume !== this.props.volume
    ) {
      this.updatePlayerController();
    }
  }

  componentWillUnmount() {
    document.body.style.backgroundColor = this.bodyBGColor;
    window.removeEventListener("resize", this.setVideoSize);
    clearInterval(this.progressInterval);

    if (this.ws) {
      this.ws.send(JSON.stringify({ playing: null }));
      this.ws.close();
    }

    const video = document.querySelector("#player-video video");
    if (true) {
      storeItem(this.props.storage, {
        storage: this.props.storage,
        language: this.state.preferentialLanguage,
        id: this.props.playingId,
        position: video.currentTime,
      });
      this.props.onChildAction({
        action: { name: "lastStatus-update", value: "" },
      });
    }
    video.pause();
    video.currentTime = 0;
  }

  onWindowControlClick = (className) => {
    if (className === "close") this.props.onChildAction({ player: null });
    else if (className === "fullscreen") {
      this.props.onChildAction({action: { name: "window-maximize" }});
      // document.querySelector("#player-video video").requestFullscreen();
    } else {
      const playerElem = document.querySelector("#player");
      if (className === "defaultplayer") {
        playerElem.style.top = "0px";
        playerElem.style.left = "0px";
      } else {
        playerElem.style.top = "auto";
        playerElem.style.left = "auto";
      }
      this.setState(
        {
          ...this.state,
          windowClass: className,
        },
        () => {
          this.setVideoSize();
          this.updatePlayerController();
        }
      );
    }
  };

  onControlsAction = (key, value) => {
    const video = document.querySelector("#player-video video");
    switch (key) {
      case "player-window":
        this.onWindowControlClick(value);
        break;
      case "pause":
        this.togglePlay();
        break;
      case "progress":
        video.currentTime = Math.round((value * video.duration) / 100);
        break;
      case "volume":
        video.volume = parseFloat(value / 100);
        this.props.onChildAction({
          action: { name: "volume-set", value },
        });
        break;
      case "preview":
        if (this.mediaIndex > 0) {
          this.mediaIndex--;
          this.play();
        }
        break;
      case "next":
        if (this.mediaIndex < this.props.data.length - 1) {
          this.mediaIndex++;
          this.play();
        }
        break;
      case "play-profile-index":
        this.mediaIndex = value;
        this.play();
        break;
      case "subtitle":
        const media = this.props.data[this.mediaIndex];
        this.setTracks(video, media.texts, value);
        break;
      case "subtitles-font":
        video.classList.remove(...video.classList);
        video.classList.add(value.size);
        video.classList.add(value.color);
        break;
      default:
        break;
    }
  };

  render() {
    const {
      data,
      volume,
      texts,
      onChildAction,
      windowIsMaximized,
      subtitles,
      language,
    } = this.props;
    const { windowClass, progress, status, casting } = this.state;
    return (
      <div
        id="player"
        className={(windowIsMaximized ? "noborders " : "") + windowClass}
      >
        <div id="player-loading" className="hidden">
          <div>
            {texts.find((text) => text.code === "loading").value.toUpperCase()}
          </div>
        </div>
        <div id="player-video" className="hidden">
          <video controls={false} autoPlay={false}>
            <track
              src="#"
              kind="subtitles"
              srcLang="en"
              label={"Language"}
              mode={"showing"}
            />
          </video>
        </div>
        <Titlebar
          caller={{
            name: "player",
            casting,
            class: windowClass,
            onAction: (action) => this.onWindowControlClick(action),
            onCast: () => {
              this.castNow();
            },
            onClose: () => onChildAction({ player: null }),
          }}
          windowIsMaximized={windowIsMaximized}
          onChildAction={onChildAction}
        />
        <div id="player-volume" className="hidden">
          volume: {volume}%
        </div>
        <div className="player-controls">
          {this.mediaIndex !== -1 && (
            <PlayerControl
              language={language}
              texts={texts}
              media={data[this.mediaIndex]}
              onControlsAction={this.onControlsAction}
              progress={progress}
              status={status}
              volume={volume}
              subtitles={subtitles}
            />
          )}
        </div>
      </div>
    );
  }
}
