import React from "react";
import "./Titlebar.css";
import playingIcon from "../../assets/images/playing-dark.gif";
import icons from "../../assets/images/icons.svg";

const Titlebar = ({ caller, windowIsMaximized, onChildAction }) => {
  return (
    <div
      id="titlebar"
      className={`${
        caller.name === "preview" ||
        (caller.name === "player" && caller.class === "defaultplayer")
          ? "autohide"
          : ""
      }`}
    >
      {caller.name === "main" && (
        <>
          <div className="logo"></div>
          <div className="appName">Media Learning Center</div>
        </>
      )}
      {caller.name === "player" && (
        <div className={`winCtrls-left ${caller.name}`}>
          {caller.casting === "off" && (
            <div className="ctrl" onClick={() => caller.onCast("start")}>
              <svg width="16" height="16">
                <use xlinkHref={`${icons}#cast-off`} />
              </svg>
            </div>
          )}
          {caller.casting === "on" && (
            <div className="ctrl" onClick={() => caller.onCast("end")}>
              <svg width="16" height="16">
                <use xlinkHref={`${icons}#cast-on`} />
              </svg>
            </div>
          )}
          {caller.class === "minimized" && (
            <div id="minimized-playing">
              <img src={playingIcon} alt="playing" />
            </div>
          )}
          {caller.class !== "minimized" && (
            <div className="ctrl" onClick={() => caller.onAction("minimized")}>
              <svg width="16" height="16">
                <use xlinkHref={`${icons}#down`} />
              </svg>
            </div>
          )}
          {caller.class !== "miniplayer" && (
            <div className="ctrl" onClick={() => caller.onAction("miniplayer")}>
              <svg width="16" height="16">
                <use xlinkHref={`${icons}#miniplayer`} />
              </svg>
            </div>
          )}
          {caller.class !== "defaultplayer" && (
            <div
              className="ctrl"
              onClick={() => caller.onAction("defaultplayer")}
            >
              <svg width="16" height="16">
                <use xlinkHref={`${icons}#defaultplayer`} />
              </svg>
            </div>
          )}
        </div>
      )}

      <div className={`winCtrls-right ${caller.name}`}>
        {caller.name === "profile" && !caller.isRemote && (
          <>
            {!caller.isEditing && (
              <div className="ctrl" onClick={caller.onEdit}>
                <svg
                  width="14"
                  height="14"
                  style={{
                    marginLeft: "2px",
                    marginRight: "-2px",
                    marginTop: "2px",
                    marginBottom: "-2px",
                  }}
                >
                  <use xlinkHref={`${icons}#edit`} />
                </svg>
              </div>
            )}
            {/* {caller.isEditing ? (
              <div className="ctrl" onClick={caller.onSave}>
                <svg
                  width="14"
                  height="14"
                  style={{
                    marginLeft: "2px",
                    marginRight: "-2px",
                    marginTop: "2px",
                    marginBottom: "-2px",
                  }}
                >
                  <use xlinkHref={`${icons}#check`} />
                </svg>
              </div>
            ) : (
              <div className="ctrl" onClick={caller.onEdit}>
                <svg
                  width="14"
                  height="14"
                  style={{
                    marginLeft: "2px",
                    marginRight: "-2px",
                    marginTop: "2px",
                    marginBottom: "-2px",
                  }}
                >
                  <use xlinkHref={`${icons}#edit`} />
                </svg>
              </div>
            )} */}
          </>
        )}
        {(caller.name !== "player" || caller.class === "defaultplayer") && (
          <>
            <div
              className="ctrl"
              onClick={() => {
                onChildAction({
                  action: { name: "window-minimize" },
                });
              }}
            >
              <svg width="16" height="16">
                <use xlinkHref={`${icons}#minimize`} />
              </svg>
            </div>
            <div
              className="ctrl"
              onClick={() =>
                onChildAction({
                  action: { name: "window-maximize" },
                })
              }
            >
              <svg width="16" height="16">
                <use
                  xlinkHref={`${icons}#${
                    windowIsMaximized ? "restore" : "maximize"
                  }`}
                />
              </svg>
            </div>
          </>
        )}
        <div
          className={`ctrl${caller.name === "scanner" ? " disabled" : ""}`}
          onClick={() => {
            if (caller.onClose) {
              caller.onClose();
            } else
              onChildAction({
                action: { name: "window-close" },
              });
          }}
        >
          <svg width="16" height="16">
            <use xlinkHref={`${icons}#close`} />
          </svg>
        </div>
      </div>
    </div>
  );
};

export default Titlebar;
