import React, { Component } from "react";
import "./Prefs.css";
import axios from "axios";
import icons from "../../assets/images/icons.svg";
import loading from "../../assets/images/loading.gif";
import Titlebar from "../commons/Titlebar";
import Dialog from "../commons/Dialog";
import { isValidUrl } from "../../utils/utils";

const { constants, ipcRenderer } = window.electron;

const FolderItem = ({ folder, placeholder, onEditFolder, onDeleteFolder }) => {
  return (
    <div className="input">
      <input
        type="text"
        readOnly={true}
        value={folder}
        placeholder={placeholder}
        onClick={onEditFolder}
      />
      <div className="btns">
        <div className={`btn`} onClick={onEditFolder}>
          <svg
            width="14"
            height="14"
            style={{ marginTop: "2px", marginBottom: "-2px" }}
          >
            <use xlinkHref={`${icons}#edit`} />
          </svg>
        </div>
        <div className={`btn`} onClick={onDeleteFolder}>
          <svg width="12" height="12">
            <use xlinkHref={`${icons}#delete`} />
          </svg>
        </div>
      </div>
    </div>
  );
};

class Preferences extends Component {
  state = {
    dialog: null,
    preferences: null,
    testingURL: false,
    validRemote: false,
  };
  original;

  checkScrollability = () => {
    const right = document.querySelector("#preferences .right");
    if (right.scrollHeight > right.clientHeight)
      right.classList.add("scrollbar");
    else right.classList.remove("scrollbar");

    const left = document.querySelector("#preferences .left");
    if (left.scrollHeight > left.clientHeight) left.classList.add("scrollbar");
    else left.classList.remove("scrollbar");
  };

  constructor(props) {
    super(props);
    this.state.preferences = JSON.parse(JSON.stringify(props.preferences));
    this.original = JSON.parse(JSON.stringify(props.preferences));
  }

  componentDidMount() {
    this.checkScrollability();
    window.addEventListener("resize", this.checkScrollability);
    ipcRenderer.on(constants.CHANNELS.FOLDER_SELECT, (result) => {
      if (result.folder !== "") {
        const preferences = this.state.preferences;
        preferences.source.local[result.category][result.index] = result.folder;
        this.setState({ ...this.state, preferences });
      }
    });
  }

  componentDidUpdate(prevProps, prevState) {
    const preferences = this.state.preferences;
    if (prevProps.preferences.theme !== prevState.preferences.theme) {
      preferences.theme = prevProps.preferences.theme;
      this.setState({ ...this.state, preferences });
    }
    if (prevProps.preferences.language !== prevState.preferences.language) {
      preferences.language = prevProps.preferences.language;
      this.setState({ ...this.state, preferences });
    }
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.checkScrollability);
  }

  showDialog = (title, errorMessage) => {
    this.setState({
      ...this.state,
      dialog: {
        title: title,
        message: errorMessage,
        buttons: [
          {
            label: "OK",
            onClick: () => {
              this.setState({ ...this.state, dialog: null });
            },
          },
        ],
        onClose: () => {
          this.setState({ ...this.state, dialog: null });
        },
      },
    });
  };

  onThemeChange = (e) => {
    this.props.onChildAction({
      action: { name: "theme-set", value: e.target.value },
    });
  };

  onLanguageChange = (e) => {
    if (this.state.dialog) this.setState({ ...this.state, dialog: null });
    this.props.onChildAction({
      action: { name: "language-set", value: e.target.value },
    });
  };

  onRemoteTest = () => {
    const texts = this.props.texts;
    let validRemote = this.state.validRemote;
    const remote = this.state.preferences.source.remote;
    if (remote.url.lastIndexOf("/") === remote.url.length - 1)
      remote.url = remote.url.substring(0, remote.url.lastIndexOf("/"));

    if (!isValidUrl(remote.url)) {
      this.showDialog(
        texts.find((text) => text.code === "prefs-atert-url-invalid").value,
        texts.find((text) => text.code === "prefs-atert-url-invalid-message")
          .value
      );
      return;
    }

    this.setState({ ...this.state, testingURL: true });
    axios
      .get(remote.url + "/api/lists")
      .then((response) => {
        if (response.data.error) {
          this.showDialog(
            texts.find((text) => text.code === "prefs-atert-url-error-response")
              .value,
            texts.find(
              (text) => text.code === "prefs-atert-url-error-response-message"
            ).value
          );
        } else {
          validRemote = true;
          this.showDialog(
            texts.find((text) => text.code === "prefs-atert-url-success").value,
            texts.find(
              (text) => text.code === "prefs-atert-url-success-message"
            ).value
          );
        }
      })
      .catch((error) => {
        this.showDialog(
          texts.find((text) => text.code === "prefs-atert-url-error-response")
            .value,
          texts.find(
            (text) => text.code === "prefs-atert-url-error-response-message"
          ).value
        );
      })
      .finally(() =>
        this.setState({ ...this.state, testingURL: false, validRemote })
      );
  };

  onRemoteInput = (e) => {
    const preferences = this.state.preferences;
    preferences.source.remote.url = e.target.value;
    this.setState({
      ...this.state,
      preferences,
      dialog: null,
      validRemote: false,
    });
  };

  onRemoteEnable = () => {
    const preferences = this.state.preferences;
    preferences.source.remote.enabled = !preferences.source.remote.enabled;
    this.setState({ ...this.state, preferences });
  };

  onStartChange = (e) => {
    const preferences = this.state.preferences;
    preferences.others.start = e.target.checked;
    this.setState({ ...this.state, preferences, dialog: null });
  };

  onAddFolder = (category) => {
    const preferences = this.state.preferences;
    preferences.source.local[category].push("");
    this.setState({ ...this.state, preferences });
  };

  onEditFolder = (category, index, folder) => {
    ipcRenderer.send(constants.CHANNELS.FOLDER_SELECT, {
      category: category,
      index: index,
      defaultPath: folder,
    });
  };

  onDeleteFolder = (category, index) => {
    const preferences = this.state.preferences;
    const nFolders = [];
    for (let i = 0; i < preferences.source.local[category].length; i++) {
      if (i !== index) nFolders.push(preferences.source.local[category][i]);
    }
    preferences.source.local[category] = nFolders;
    this.setState({ ...this.state, preferences });
  };

  onSubtitlesSizeChange = (e) => {
    const preferences = this.state.preferences;
    preferences.subtitles.size = e.target.value;
    this.setState({ ...this.state, preferences });
  };
  onSubtitlesColorChange = (e) => {
    const preferences = this.state.preferences;
    preferences.subtitles.color = e.target.value;
    this.setState({ ...this.state, preferences });
  };

  onPortInput = (e) => {
    if (
      e.target.value !== "" &&
      (e.target.value.startsWith(".") || !e.target.value.match(/^\d+$/))
    ) {
      return;
    }
    const value = parseInt(e.target.value);
    if (value > 99999) return;
    const preferences = this.state.preferences;
    preferences.server.port = value;
    this.setState({
      ...this.state,
      preferences,
      dialog: null,
      validLocalPort: false,
    });
  };

  // onPasswordInput = (e) => {
  //   const preferences = this.state.preferences;
  //   preferences.server.password = e.target.value;
  //   this.setState({ ...this.state, preferences });
  // };

  onLocalOnlyChange = (e) => {
    const preferences = this.state.preferences;
    preferences.server.localOnly = e.target.checked;
    this.setState({ ...this.state, preferences, dialog: null });
  };

  onSave = () => {
    const { preferences, validRemote, testingURL } = this.state;
    if (testingURL) return;

    const changes = {};

    if (
      this.original.source.remote.url !== preferences.source.remote.url ||
      this.original.source.remote.enabled !== preferences.source.remote.enabled
    ) {
      if (preferences.source.remote.enabled && !validRemote) {
        const texts = this.props.texts;
        this.showDialog(
          texts.find((text) => text.code === "prefs-atert-url-invalid").value,
          texts.find((text) => text.code === "prefs-atert-url-invalid-message")
            .value
        );
        return;
      }
      changes.remote = preferences.source.remote;
    }

    // const reset = {
    //   music: document.querySelector("#reset-music").checked,
    //   series: document.querySelector("#reset-series").checked,
    //   movies: document.querySelector("#reset-movies").checked,
    //   books: document.querySelector("#reset-books").checked,
    //   courses: document.querySelector("#reset-courses").checked,
    // };
    const reset = {
      music: false,
      series: false,
      movies: false,
      books: false,
      courses: false,
    };

    if (
      JSON.stringify(this.original.server) !==
      JSON.stringify(preferences.server)
    ) {
      changes.server = preferences.server;
    }

    if (this.original.subtitles !== preferences.subtitles) {
      changes.subtitles = preferences.subtitles;
    }

    if (this.original.others.start !== preferences.others.start) {
      changes.others = preferences.others;
    }

    if (
      JSON.stringify(this.original.source.local) !==
      JSON.stringify(preferences.source.local)
    ) {
      changes.folders = preferences.source.local;
    }

    if (JSON.stringify(reset).includes("true")) {
      changes.reset = reset;
    }

    if (
      "remote" in changes ||
      "folders" in changes ||
      "reset" in changes ||
      "others" in changes ||
      "subtitles" in changes ||
      "server" in changes
    )
      this.props.onChildAction({
        action: { name: "prefs-set", value: changes },
      });

    this.props.onChildAction({
      component: { name: "main" },
    });
  };

  onCancel = () => {
    if (this.state.testingURL) return;
    const preferences = this.state.preferences;
    if (this.original.theme !== preferences.theme) {
      this.props.onChildAction({
        action: { name: "theme-set", value: this.original.theme },
      });
    }
    if (this.original.language !== preferences.language) {
      this.props.onChildAction({
        action: { name: "language-set", value: this.original.language },
      });
    }
    setTimeout(() => {
      this.props.onChildAction({
        component: { name: "main" },
      });
    }, 300);
  };

  render() {
    const { windowIsMaximized, texts, categories, languages, onChildAction } =
      this.props;
    const { preferences, testingURL, validRemote, dialog } = this.state;
    return (
      <div id="preferences">
        <Titlebar
          caller={{
            name: "preferences",
            onClose: () => this.onCancel(),
          }}
          windowIsMaximized={windowIsMaximized}
          onChildAction={onChildAction}
        />
        <header>
          <div>{texts.find((text) => text.code === "prefs-title").value}</div>
          <div>
            {texts.find((text) => text.code === "prefs-subtitle").value}
          </div>
        </header>
        <main>
          <div className="centered">
            <div className="left">
              <div className="label">
                <div>
                  {texts.find((text) => text.code === "prefs-theme").value}
                </div>
              </div>
              <div className="options">
                <label>
                  <input
                    type="radio"
                    name="theme"
                    value={"light"}
                    checked={preferences.theme === "light"}
                    onChange={this.onThemeChange}
                  />
                  {
                    texts.find((text) => text.code === "prefs-theme-light")
                      .value
                  }
                </label>
                <label>
                  <input
                    type="radio"
                    name="theme"
                    value={"dark"}
                    checked={preferences.theme === "dark"}
                    onChange={this.onThemeChange}
                  />
                  {texts.find((text) => text.code === "prefs-theme-dark").value}
                </label>
                <label>
                  <input
                    type="radio"
                    name="theme"
                    value={"system"}
                    checked={preferences.theme === "system"}
                    onChange={this.onThemeChange}
                  />
                  {
                    texts.find((text) => text.code === "prefs-theme-system")
                      .value
                  }
                </label>
              </div>
              <div className="label">
                <div>
                  {texts.find((text) => text.code === "prefs-language").value}
                </div>
              </div>
              <div className="options">
                {languages.map((language) => {
                  return (
                    <label key={language.code}>
                      <input
                        type="radio"
                        name="language"
                        value={language.code}
                        checked={preferences.language === language.code}
                        onChange={this.onLanguageChange}
                      />
                      {language.name}
                    </label>
                  );
                })}
              </div>
              <div className="label">
                <div>
                  {texts.find((text) => text.code === "prefs-remote").value}
                </div>
                <div>
                  {
                    texts.find(
                      (text) => text.code === "prefs-remote-explanation"
                    ).value
                  }
                </div>
              </div>
              <div className="options">
                <div className="input">
                  <input
                    type="url"
                    value={preferences.source.remote.url}
                    placeholder={`{${
                      texts.find(
                        (text) => text.code === "prefs-remote-protocol"
                      ).value
                    }}://{ip}:{${
                      texts.find((text) => text.code === "prefs-remote-port")
                        .value
                    }}`}
                    onInput={this.onRemoteInput}
                  />
                  <div className="btns">
                    {testingURL ? (
                      <div className={`btn`}>
                        <img src={loading} alt="testing..." />
                      </div>
                    ) : (
                      <div
                        className={`btn${validRemote ? " positive" : ""}`}
                        onClick={this.onRemoteTest}
                      >
                        <svg
                          width="16"
                          height="16"
                          style={{ marginTop: "2px", marginBottom: "-2px" }}
                        >
                          <use xlinkHref={`${icons}#check`} />
                        </svg>
                      </div>
                    )}
                    <div
                      className={`btn${
                        preferences.source.remote.enabled ? " positive" : ""
                      }`}
                      onClick={this.onRemoteEnable}
                    >
                      <svg
                        width="16"
                        height="16"
                        style={{ marginTop: "1px", marginBottom: "-1px" }}
                      >
                        <use
                          xlinkHref={`${icons}#${
                            preferences.source.remote.enabled
                              ? "checked"
                              : "unchecked"
                          }`}
                        />
                      </svg>
                    </div>
                  </div>
                </div>
              </div>
              <div className="label">
                <div>
                  {texts.find((text) => text.code === "prefs-serving").value}
                </div>
                <div>
                  {
                    texts.find(
                      (text) => text.code === "prefs-serving-explanation"
                    ).value
                  }
                </div>
              </div>
              <div className="options">
                <div className="inputs">
                  <div className="input quarter notlast">
                    <input
                      type="text"
                      placeholder={`{${
                        texts.find((text) => text.code === "prefs-serving-port")
                          .value
                      }}`}
                      value={preferences.server.port}
                      onInput={this.onPortInput}
                    />
                  </div>
                  <label>
                    <input
                      type="checkbox"
                      name="localOnly"
                      checked={preferences.server.localOnly}
                      onChange={this.onLocalOnlyChange}
                    />
                    {
                      texts.find(
                        (text) => text.code === "prefs-serving-localOnly"
                      ).value
                    }
                  </label>
                </div>
              </div>

              <div className="label">
                <div>
                  {texts.find((text) => text.code === "prefs-subtitles").value}
                </div>
              </div>
              <div className="options">
                <label>
                  <input
                    type="radio"
                    name="font-size"
                    value={"smallFont"}
                    checked={preferences.subtitles.size === "smallFont"}
                    onChange={this.onSubtitlesSizeChange}
                  />
                  {
                    texts.find(
                      (text) => text.code === "prefs-subtitles-size-small"
                    ).value
                  }
                </label>
                <label>
                  <input
                    type="radio"
                    name="font-size"
                    value={"mediumFont"}
                    checked={preferences.subtitles.size === "mediumFont"}
                    onChange={this.onSubtitlesSizeChange}
                  />
                  {
                    texts.find(
                      (text) => text.code === "prefs-subtitles-size-medium"
                    ).value
                  }
                </label>
                <label>
                  <input
                    type="radio"
                    name="font-size"
                    value={"bigFont"}
                    checked={preferences.subtitles.size === "bigFont"}
                    onChange={this.onSubtitlesSizeChange}
                  />
                  {
                    texts.find(
                      (text) => text.code === "prefs-subtitles-size-big"
                    ).value
                  }
                </label>
                {` `}|{` `}
                <label>
                  <input
                    type="radio"
                    name="font-color"
                    value={"yellowFont"}
                    checked={preferences.subtitles.color === "yellowFont"}
                    onChange={this.onSubtitlesColorChange}
                  />
                  {
                    texts.find(
                      (text) => text.code === "prefs-subtitles-color-yellow"
                    ).value
                  }
                </label>
                <label>
                  <input
                    type="radio"
                    name="font-color"
                    value={"whiteFont"}
                    checked={preferences.subtitles.color === "whiteFont"}
                    onChange={this.onSubtitlesColorChange}
                  />
                  {
                    texts.find(
                      (text) => text.code === "prefs-subtitles-color-white"
                    ).value
                  }
                </label>
              </div>

              <div className="label">
                <div>
                  {texts.find((text) => text.code === "prefs-others").value}
                </div>
              </div>
              <div className="options">
                <label>
                  <input
                    type="checkbox"
                    name="start"
                    checked={preferences.others.start}
                    onChange={this.onStartChange}
                  />
                  {
                    texts.find((text) => text.code === "prefs-others-start")
                      .value
                  }
                </label>
              </div>
            </div>
            <div className="right">
              <div className="label">
                <div>
                  {texts.find((text) => text.code === "prefs-local").value}
                </div>
                <div>
                  {
                    texts.find(
                      (text) => text.code === "prefs-local-explanation"
                    ).value
                  }
                </div>
              </div>
              {categories.map((category) => {
                const folders = preferences.source.local[category.code];
                const addDisabled =
                  folders.length === 0 || folders[folders.length - 1] === "";
                return (
                  <div key={category.code} className="folder">
                    <button
                      className="link rounded"
                      onClick={() => {
                        if (!addDisabled) this.onAddFolder(category.code);
                      }}
                    >
                      <svg width="10" height="10">
                        <use xlinkHref={`${icons}#plus`} />
                      </svg>
                      <span>
                        {
                          texts.find((text) => text.code === category.code)
                            .value
                        }
                      </span>
                    </button>
                    {folders.length === 0 && (
                      <FolderItem
                        key={category.code + "-x"}
                        folder={""}
                        placeholder={""}
                        onEditFolder={() =>
                          this.onEditFolder(category.code, 0, "")
                        }
                      />
                    )}
                    {folders.map((f, idx) => (
                      <FolderItem
                        key={category.code + "-" + idx}
                        folder={f}
                        placeholder={""}
                        onEditFolder={() =>
                          this.onEditFolder(category.code, idx, f)
                        }
                        onDeleteFolder={() =>
                          this.onDeleteFolder(category.code, idx)
                        }
                      />
                    ))}
                  </div>
                );
              })}
            </div>
          </div>
        </main>
        <footer>
          <button className="accent rounded" onClick={this.onSave}>
            <span>
              {texts.find((text) => text.code === "prefs-btn-save").value}
            </span>
          </button>
          <button className="accent rounded" onClick={this.onCancel}>
            <span>
              {texts.find((text) => text.code === "prefs-btn-cancel").value}
            </span>
          </button>
        </footer>
        {dialog && (
          <Dialog
            theme={preferences.theme}
            title={dialog.title}
            message={dialog.message}
            buttons={dialog.buttons}
            onClose={dialog.onClose}
          />
        )}
      </div>
    );
  }
}

export default Preferences;
