import React, { Component } from "react";
import "./Scanner.css";
import Titlebar from "../commons/Titlebar";

class Scanner extends Component {
  getPercentage = (numbers) => {
    return (100 * (numbers[0] + numbers[1])) / numbers[2];
  };

  render() {
    const { windowIsMaximized, status, texts, onChildAction } = this.props;
    return (
      <div id="scanner">
        <Titlebar
          caller={{
            name: "scanner",
            onClose: () => console.log("onClose"),
            // onClose: () =>
            //   onChildAction({
            //     action: { name: "window-close" },
            //   }),
          }}
          windowIsMaximized={windowIsMaximized}
          onChildAction={onChildAction}
        />
        <header>
          <div>{texts.find((text) => text.code === "title").value}</div>
          <div>{texts.find((text) => text.code === "subtitle").value}</div>
        </header>
        <main className="numbers">
          <div className="items">
            {status &&
              Object.keys(status).map((key) => {
                if (key === "last") return null;
                const item = status[key];
                return (
                  <div
                    key={"scanner-" + key}
                    className={`item${
                      item.numbers[2] === 0 ? " disabled" : ""
                    }`}
                  >
                    <div className="name">
                      {texts.find((text) => text.code === key).value}
                    </div>
                    <div className="labels">
                      <div className="label-number">
                        {item.numbers[0] + item.numbers[1]}
                      </div>
                      <div className="label-media">
                        {(item.scanning.includes("\\") ||
                        item.scanning.includes("/") ||
                        item.scanning === ""
                          ? ""
                          : texts.find(
                              (text) =>
                                text.code === "scanner-core-info-retrieving"
                            ).value + ": ") + item.scanning}
                      </div>
                      <div
                        className={`label-total${
                          item.numbers[0] + item.numbers[1] ===
                            item.numbers[2] && item.numbers[2] !== 0
                            ? " finished"
                            : ""
                        }`}
                      >
                        {item.numbers[2]}
                      </div>
                    </div>
                    <div className="progress">
                      <div
                        style={{
                          width: `${this.getPercentage(item.numbers)}%`,
                        }}
                      ></div>
                    </div>
                  </div>
                );
              })}
          </div>
        </main>
        <footer className="buttons">
          <button
            className="accent rounded"
            onClick={() => {
              onChildAction({
                action: { name: "window-hide" },
              });
            }}
          >
            <span>
              {texts.find((text) => text.code === "go-background").value}
            </span>
          </button>
          <button
            className="danger rounded"
            onClick={() =>
              onChildAction({
                action: { name: "window-close" },
              })
            }
          >
            <span>{texts.find((text) => text.code === "stop-exit").value}</span>
          </button>
        </footer>
      </div>
    );
  }
}
export default Scanner;
