import React, { useEffect } from "react";
import "./Statusbar.css";
import icons from "../../assets/images/icons.svg";
import loading from "../../assets/images/loading.gif";
import playing from "../../assets/images/playing.gif";
import playingDark from "../../assets/images/playing-dark.gif";
import { getElementOffset } from "../../utils/utils";

const tagsRender = (category, onChildAction) => {
  return category.tags.map((tag) => (
    <div
      key={tag.label}
      className={`tag${tag.active ? " checked" : ""}`}
      onClick={(e) => {
        tag.active = !tag.active;
        onChildAction({
          action: { name: "category-" + category.code, value: category },
        });
      }}
    >
      <div>{tag.active && <span>✓</span>}</div>
      {tag.label}
    </div>
  ));
};

const Statusbar = ({ colorScheme, category, texts, status, onChildAction }) => {
  useEffect(() => {
    const pos = getElementOffset(document.querySelector("#statusbar"));
    document.querySelector("#main-menu").style.top =
      parseInt(pos.top) +
      document.querySelector("#statusbar").offsetHeight +
      1 +
      "px";
  }, []);

  const onInput = (e) => {
    category.terms = e.target.value;
    onChildAction({
      action: { name: "category-" + category.code, value: category },
    });
  };

  const onClear = () => {
    category.terms = "";
    onChildAction({
      action: { name: "category-" + category.code, value: category },
    });
  };

  const onToggleTags = () => {
    document.querySelector("#tags").classList.toggle("closed");
  };

  const onToggleMenu = () => {
    document.querySelector("#main-menu").classList.toggle("closed");
  };

  const onControllerChange = (e) => {
    // status.serving.controller = e.target.checked;
    onChildAction({
      action: { name: "server-controller", value: e.target.checked },
    });
  };

  return (
    <div id="statusbar">
      <div className="status">
        {status.working && (
          <div className="ctrl tooltip top">
            <div className="message">{status.working}</div>
            <img src={loading} alt="loading..." />
          </div>
        )}
        {status.playing && (
          <div className="ctrl tooltip top">
            <img
              src={colorScheme === "light" ? playing : playingDark}
              alt="playing..."
            />
            {/* <div class="tooltiptext">playing...</div> */}
          </div>
        )}
        {status.casting && (
          <div className="ctrl tooltip top">
            <svg
              width="16"
              height="16"
              style={{ marginTop: "2px", marginBottom: "-2px" }}
            >
              <use xlinkHref={`${icons}#casting`} />
            </svg>
            {/* <div class="tooltiptext">casting...</div> */}
          </div>
        )}
        {status.serving && status.serving.enabled && (
          <div className="ctrl tooltip top">
            <svg width="12" height="12">
              <use xlinkHref={`${icons}#serving`} />
            </svg>
            {/* <div class="tooltiptext">serving...</div> */}
          </div>
        )}
        {status.remote !== "" && (
          <div className="ctrl tooltip top">
            <svg
              width="15"
              height="15"
              style={{ marginTop: "1px", marginBottom: "-1px" }}
            >
              <use xlinkHref={`${icons}#cloud`} />
            </svg>
            {/* <div class="tooltiptext">connected to remote server</div> */}
          </div>
        )}
        <div className="filter">
          <input
            type="text"
            value={category.terms}
            onInput={(e) => onInput(e)}
            spellCheck={false}
            placeholder={
              texts.find((text) => text.code === category.code + "-placeholder")
                .value
            }
          />
          <div className="buttons">
            {category.terms === "" ? (
              <button className={`noaction${false ? " positive" : ""}`}>
                <svg width="16" height="16">
                  <use xlinkHref={`${icons}#filter`} />
                </svg>
              </button>
            ) : (
              <button onClick={() => onClear()}>
                <svg width="12" height="12">
                  <use xlinkHref={`${icons}#close`} />
                </svg>
              </button>
            )}
            {category.tags.length > 0 && (
              <button
                id="hashtag"
                onClick={() => onToggleTags()}
                className={`${false ? " positive" : ""}`}
              >
                <svg width="17" height="17">
                  <use xlinkHref={`${icons}#hashtag`} />
                </svg>
              </button>
            )}
          </div>
          <div id="tags" className="closed">
            {tagsRender(category, onChildAction)}
          </div>
        </div>
        <div id="main-menu-btn" className="ctrl" onClick={() => onToggleMenu()}>
          <svg width="12" height="12">
            <use xlinkHref={`${icons}#menu-dots`} />
          </svg>
        </div>
      </div>
      <div id="main-menu" className="closed">
        <div
          className="menu-item"
          onClick={() => onChildAction({ component: { name: "prefs" } })}
        >
          <div>
            {texts.find((text) => text.code === "menu-preferences").value}
          </div>
          <div></div>
        </div>
        <div className="menu-item">
          <div
            onClick={() =>
              onChildAction({
                action: {
                  name: "server-set",
                  value: status.serving ? !status.serving.enabled : true,
                },
              })
            }
          >
            {texts.find((text) => text.code === "menu-local-server").value}
          </div>
          {status.serving && status.serving.enabled ? (
            <>
              <div>{status.serving.url}</div>
              <div>
                <label>
                  <input
                    type="checkbox"
                    name="controller"
                    checked={status.serving.controller}
                    onChange={onControllerChange}
                  />
                  {
                    texts.find(
                      (text) => text.code === "menu-local-server-controller"
                    ).value
                  }
                </label>
              </div>
            </>
          ) : (
            <div className="uppercase">
              {
                texts.find((text) => text.code === "menu-local-server-disabled")
                  .value
              }
            </div>
          )}
        </div>
        {status.remote !== "" && (
          <div className="menu-item">
            <div>
              {texts.find((text) => text.code === "menu-remote-server").value}
            </div>
            <div>{status.remote}</div>
            <div>
              <button
                className="gray rounded"
                onClick={() => {
                  onToggleMenu();
                  onChildAction({ controller: true });
                }}
              >
                <svg width="14" height="12">
                  <use xlinkHref={`${icons}#remote-control`} />
                </svg>
                <span>
                  {
                    texts.find(
                      (text) => text.code === "menu-remote-server-controller"
                    ).value
                  }
                </span>
              </button>
            </div>
          </div>
        )}
        <div
          className="menu-item"
          onClick={() => onChildAction({ component: { name: "about" } })}
        >
          <div>{texts.find((text) => text.code === "menu-about").value}</div>
          <div></div>
        </div>
      </div>
    </div>
  );
};

export default Statusbar;
